<template>
  <mrc_ess_skLoaders :skLoading='skLoading'>
    <div data-app='true'
         class="data_dict">
      <mrc_ess_iconGroup :is_disabled="disabled"
                         @handleReturn='handleReturn'
                         @handleEdit='handleEdit'
                         @handleDel='handleDel'></mrc_ess_iconGroup>

      <v-form ref="add_dataDict"
              v-model="add_dataDict">
        <v-card style='padding: 10px 40px'>
          <v-row style="display: flex;
              justify-content: space-between;
              align-items: center;">
            <v-col class="d-flex"
                   cols="12"
                   sm="4"
                   dence>
              <v-switch v-model="is_used"
                        :label="is_used? $t('label_To_enable_the'): $t('label_disable')"
                        color="primary"
                        :readonly="disabled"></v-switch>
            </v-col>
            <!-- v-if="$store.state.uuid" -->
            <v-col class="d-flex justify-end"
                   cols="12"
                   sm="4">
              <div v-if="$store.state.uuid">
                <template v-for="item in reqParams.client_type">
                  <mrc_ess_svgIcon :key='item'
                                   style="font-size: 24px;margin-right: 5px"
                                   :icon-class='item'></mrc_ess_svgIcon>
                </template>
              </div>
            </v-col>
          </v-row>
          <v-row>
            <v-col class="d-flex"
                   cols="12"
                   sm="12">
              <mrc_ess_multiText :label="$t('label_Name_of_the_dictionary') + '*'"
                                 :isNational="judegNational('dict_name')"
                                 :readonly="disabled"
                                 style="width: 100%"
                                 :rules="$text_rules($t('error.dict_name'))"
                                 v-model="reqParams.dict_name"></mrc_ess_multiText>
              <!-- <v-text-field :label="$t('label_Name_of_the_dictionary') + '*'"
                            :readonly="disabled"
                            v-model="reqParams.dict_name"
                            required
                            :rules="$text_rules($t('error.dict_name'))"></v-text-field> -->
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="12">
              <!-- <v-select v-model="reqParams.client_type"
                        :readonly="disabled"
                        :items="client_types"
                        item-value="dict_value"
                        item-text="dict_label"
                        :label="$t('label.client_type') + '*'"
                        multiple
                        chips
                        :rules="[v => v.length > 0 || $t('error.cilent_type')]"></v-select> -->
              <mrc_ess_dictName v-model="reqParams.client_type"
                                :disabled='disabled'
                                :label="$t('label_Client_type') + '*'"
                                :rules="false"
                                :errMsg="$errors('reqParams','client_type',requiredRule)"
                                multiple
                                dict_name='client_type'
                                chips></mrc_ess_dictName>
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <mrc_ess_multiText :label="$t('label_Save_the_value') + '*'"
                                 :isNational="judegNational('dict_value')"
                                 :readonly="disabled"
                                 style="width: 100%"
                                 :rules="$text_rules($t('error.save_value'))"
                                 v-model="reqParams.dict_value"></mrc_ess_multiText>
              <!-- <v-text-field :readonly="disabled"
                            v-model="reqParams.dict_value"
                            required
                            :rules="$text_rules($t('error.save_value'))"
                            :label="$t('label_Save_the_value') + '*'"></v-text-field> -->
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="6">
              <mrc_ess_multiText :label="$t('label_Show_the_label') + '*'"
                                 :isNational="judegNational('dict_label')"
                                 :readonly="disabled"
                                 style="width: 100%"
                                 :rules="$text_rules($t('error.save_value'))"
                                 v-model="reqParams.dict_label"></mrc_ess_multiText>
              <!-- <v-text-field :readonly="disabled"
                            v-model="reqParams.dict_label"
                            required
                            :rules="$text_rules($t('error.show_label'))"
                            :label="$t('label_Show_the_label') + '*'"></v-text-field> -->
            </v-col>
            <v-col class="d-flex"
                   cols="12"
                   sm="12">
              <mrc_ess_NumInput :readonly="!disabled"
                                :label="$t('label_The_sorting')+'*'"
                                v-model="reqParams.sequence"></mrc_ess_NumInput>
              <!-- <v-text-field :readonly="disabled"
                            v-model.number="reqParams.sequence"
                            required
                            @blur='verifyNum'
                            :rules="$text_rules($t('label.sort'),[ v => /^[1-9]*$/.test(v) || $t('error.sort')])"
                            :label="$t('label_The_sorting') + '*'"></v-text-field> -->
            </v-col>
          </v-row>
        </v-card>
        <mrc_ess_submitBtn v-show="!disabled"
                           :loading='loading'
                           @cancel='cancel'
                           @success='success'></mrc_ess_submitBtn>
      </v-form>
      <!-- 删除确认框 -->
      <confirm :isOpen='isOpen'
               :showText='showText'
               @onCancel='onCancel'
               @onConfirm='onConfirm'
               :isLoading='isLoading' />
      <v-snackbar v-model="snackbar"
                  top
                  :timeout='4000'>
        {{ snackbartext }}

      </v-snackbar>
    </div>
  </mrc_ess_skLoaders>

</template>

<script>
import { getAdd, getDataByName, getDelete, getDetail, getChange } from '@/service/api/data_dictionary'
import confirm from '@/components/confirm'
// import myBtns from '@/components/c_s_btns'
// import iconGroup from '@/components/iconGroup'
import { required } from 'vuelidate/lib/validators'
export default {
  components: { confirm },
  props: {},
  data () {
    return {
      loading: false,
      skLoading: true,
      snackbar: false,
      snackbartext: '',
      showText: this.$t('info.is_del'),
      is_change: 1, // 是否有删除的权限
      isLoading: false,
      is_return: false,
      isOpen: false,
      disabled: true,
      add_dataDict: true,
      is_used: true,
      params: {
        '~table~': 'mrbase_sys_dict'
      },
      reqParams: {
        '~table~': 'mrbase_sys_dict',
        uuid: '',
        dict_name: '', // 字典名称
        dict_label: '', // 显示标签
        dict_value: '', // 保存值
        is_used: '', // 是否启用
        client_type: [], // 客户端类型
        sequence: 1 // 排序
      }
    };
  },
  validations: {
    reqParams: {
      client_type: {
        required
      }
    }
  },
  created () { },
  async mounted () {
    // 有uuid传过来 就是修改 
    await this.national('mrbase_sys_dict')
    this.skLoading = true
    const name = this.$store.getters.subPage_name
    const uuid = this.$route.params.uuid || this.$store.state.uuid
    const is_change = this.$route.params.is_change || this.$store.state.is_change
    if (is_change == 1) {
      this.is_change = is_change
    }
    if (uuid) {
      this.$route.meta.title = name
      this.reqParams.uuid = uuid

      // 查询详情
      const detailObj = await getDetail({ uuid: this.reqParams.uuid, '~table~': this.params['~table~'] })
      // console.log(detailObj)
      // const data = detailObj.data
      detailObj.data.client_type = detailObj.data.client_type ? detailObj.data.client_type.split(',') : []
      // for(var key in data) {
      //   for(var a in this.reqParams) {
      //     if (key == a) {
      //       this.reqParams[a] = data[key]
      //     }
      //   }
      // }
      console.log(this.reqParams, '-----', detailObj.data);
      this.reqParams = detailObj.data
      // console.log(this.reqParams.client_type,'client-type')
      // this.$route.meta.title = this.INT(this.reqParams.dict_name)
      // this.reqParams.client_type = this.reqParams.client_type ?this.reqParams.client_type.split(',') : []
      this.reqParams.is_used ? this.is_used = true : this.is_used = false
      this.temp = this.reqParams
      if (!this.$store.state.iconstatus) {
        this.disabled = false
      }
    } else {
      this.$route.meta.title = '新字典'
      this.disabled = false
    }
    this.skLoading = false
  },
  methods: {

    // 验证排序是否是数字 不是数字就重置为0
    verifyNum (e) {
      if (!/^[1-9]*$/.test(Number(e.target.value))) {
        this.reqParams.sequence = 1
        e.target.value = 1
      }
    },
    // 点击修改icon
    handleEdit () {
      this.disabled = !this.disabled
    },
    // 点击返回上一页
    handleReturn () {
      this.$router.go(-1)
    },
    // 点击删除此参照
    handleDel () {
      this.isOpen = true
      this.showText = this.$t('info.is_del')
    },
    // 二次确认 取消
    onCancel () {
      this.is_return = false
      this.isOpen = false
    },
    // 二次确认 确定
    async onConfirm () {
      // 确认放弃修改
      if (this.is_return) {
        this.$router.go(-1)
      } else {
        // 点的删除
        this.isLoading = true
        const obj = Object.assign({}, { uuid: this.reqParams.uuid, '~table~': this.params['~table~'] })
        await getDelete(obj)
        this.isOpen = false
        this.isLoading = false
        console.log(this.$parent.desserts.length === 1 && this.$parent.params.cpage > 1)
        if (this.$parent.desserts.length === 1 && this.$parent.params.cpage > 1) {
          this.$parent.params.cpage--
          this.$parent.params.totalpage--
        }
        this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
        this.$router.go(-1)
      }

    },
    // 取消 修改或者添加
    cancel () {
      const uuid = this.$route.params.uuid || this.$store.state.uuid
      this.$myConfirm({ content: this.$t('message_Confirm_the_cancellation_of_the_edit') })
        .then(() => {
          this.$router.go(-1)
        }).catch(() => { })
      // if (uuid) {

      //   this.showText = this.$t('info.give_up_modify')
      // } else {
      //   this.showText = this.$t('info.give_up_add')
      // }
      // this.isOpen = true
      // this.is_return = true
    },
    // 提交
    async success () {
      this.$v.reqParams.client_type.$touch()
      const isSubmit = this.$refs.add_dataDict.validate()
      if (!isSubmit || this.$v.reqParams.client_type.$error) {
        this.is_return = false
        this.isOpen = false
        return
      }
      this.isLoading = true
      this.reqParams.is_used = this.is_used ? 1 : 0
      // 深拷贝要传的数据，  因为那个客户端呢类型数组要转成字符串
      let obj = this.$clone(this.reqParams)
      obj.client_type = obj.client_type.join()
      // return
      // 修改
      this.loading = true
      if (this.reqParams.uuid) {
        // return
        const res = await getChange({
          '~table~': this.params['~table~'],
          uuid: obj.uuid,
          dict_name: obj.dict_name, // 字典名称
          dict_label: obj.dict_label, // 显示标签
          dict_value: obj.dict_value, // 保存值
          is_used: obj.is_used, // 是否启用
          client_type: obj.client_type, // 客户端类型
          sequence: obj.sequence // 排序
        })
        if (res.code == 200) {
          this.$store.commit('newsTips', this.$t('info.edit_success'))
          this.disabled = true
          this.temp = this.reqParams
          this.$router.go(-1)
        }
        this.loading = false

      } else {
        // 添加
        const res = await getAdd({
          '~table~': this.params['~table~'],
          // uuid: obj.uuid,
          dict_name: obj.dict_name, // 字典名称
          dict_label: obj.dict_label, // 显示标签
          dict_value: obj.dict_value, // 保存值
          is_used: obj.is_used, // 是否启用
          client_type: obj.client_type, // 客户端类型
          sequence: obj.sequence // 排序
        })
        if (res.code == 200) {
          this.$store.commit('newsTips', this.$t('info.add_success'))
          this.$router.go(-1)
        }
        this.loading = false
      }
      this.isLoading = false
    }
  },
  computed: {
  },
  watch: {
  },
  beforeDestroy () { }
};
</script>

<style scoped>
.data_dict /deep/ .v-menu__content {
  min-width: 400px !important;
}
.iconGroup {
  position: relative;
}
.returnBtn {
  color: #fff;
  position: absolute;
  top: -55px;
  right: 130px;
}
.editBtn {
  color: #fff;
  position: absolute;
  top: -55px;
  right: 70px;
}
.delBtn {
  color: #fff;
  position: absolute;
  top: -55px;
  right: 10px;
}
</style>
